p {
  margin: 0;
  padding: 0;
}

ul, ol {
  list-style: none;
}

body {
  margin-left: auto;
  margin-right: auto;
  font-family: DM Sans, sans-serif;
}

.section {
  padding-bottom: 60px;
}

@keyframes girlAnim {
  0%, 100% {
    transform: translateX(0);
  }

  10%, 30%, 50%, 70% {
    transform: translateX(-5px);
  }

  20%, 40%, 60% {
    transform: translateX(5px);
  }

  80% {
    transform: translateX(3px);
  }

  90% {
    transform: translateX(-3px);
  }
}

@keyframes CircleFranchise {
  0% {
    transform-origin: center;
    animation-timing-function: ease-out;
    transform: scale(1);
  }

  10% {
    animation-timing-function: ease-in;
    transform: scale(.91);
  }

  17% {
    animation-timing-function: ease-out;
    transform: scale(.98);
  }

  33% {
    animation-timing-function: ease-in;
    transform: scale(.87);
  }

  45% {
    animation-timing-function: ease-out;
    transform: scale(1);
  }
}

@keyframes hero-title {
  0% {
    opacity: 0;
    transform: translateX(-50px);
  }

  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes hero-title-accent {
  0% {
    opacity: 0;
    transform: scale(.6);
  }

  100% {
    opacity: 1;
    transform: scale(1);
  }
}

@keyframes header-animation {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes btnAnim {
  0%, 100% {
    transform: translateY(0);
  }

  10%, 30%, 50%, 70% {
    transform: translateY(-1px);
  }

  20%, 40%, 60% {
    transform: translateY(1px);
  }

  80% {
    transform: translateY(1px);
  }

  90% {
    transform: translateY(-1px);
  }
}

.button {
  width: fit-content;
  height: fit-content;
  color: #fff;
  fill: #fff;
  cursor: pointer;
  height: 24px;
  white-space: nowrap;
  background-color: #d41443;
  border: none;
  border-radius: 20px;
  outline: none;
  justify-content: center;
  align-items: center;
  gap: 10%;
  padding-left: 16px;
  padding-right: 16px;
  font-family: DM Sans, sans-serif;
  font-size: 8px;
  font-weight: 400;
  line-height: 1.25;
  text-decoration: none;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-flex;
}

.button:hover, .button:focus {
  fill: #d41443;
  background-color: #c7133e;
}

@media screen and (min-width: 1200px) {
  .button {
    height: 40px;
    padding-left: 24px;
    padding-right: 24px;
    font-size: 14px;
    line-height: 1.29;
  }
}

.button--secondary {
  color: #d41443;
  fill: #d41443;
  background-color: #fff;
}

.button--secondary:hover, .button--secondary:focus {
  background-color: #ecebeb;
}

.button--large {
  height: 44px;
  border-radius: 22px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.31;
}

.button__animation {
  animation: hero-title-accent 2s cubic-bezier(.5, 1, .89, 1) 2s both;
}

.header {
  background-color: #ffb8ca;
}

.header__item {
  padding-top: 8px;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__item {
    padding-top: 32px;
  }
}

@media screen and (min-width: 1200px) {
  .header__item {
    align-items: center;
    padding-top: 37px;
  }
}

.header__logo {
  animation: header-animation 2s cubic-bezier(.5, 1, .89, 1) both;
}

.header__buttons {
  margin-left: auto;
  display: flex;
}

@media screen and (min-width: 768px) {
  .header__buttons {
    align-items: baseline;
  }
}

@media screen and (max-width: 1199.98px) {
  .header__menu {
    max-width: 320px;
    width: 100%;
    height: 100%;
    z-index: 5;
    background-color: #e17992;
    padding-top: 15px;
    transition: transform .25s cubic-bezier(.4, 0, .2, 1);
    position: fixed;
    top: 0;
    right: 0;
    transform: translateX(100%);
  }
}

@media screen and (max-width: 1199.98px) and (min-width: 768px) {
  .header__menu {
    max-width: 254px;
  }
}

.header__menu.is-open {
  z-index: 5;
  transform: translateX(0%);
}

.header__inner {
  max-width: 480px;
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

@media screen and (min-width: 1200px) {
  .header__inner {
    display: none;
  }
}

.menu-button {
  cursor: pointer;
  background: none;
  border: none;
  margin-left: auto;
}

@media screen and (min-width: 1200px) {
  .menu-button {
    display: none;
  }
}

.menu-button__open {
  padding-left: 0;
  padding-right: 0;
}

@media screen and (min-width: 768px) {
  .menu-button__open {
    margin-right: 32px;
  }
}

.menu-button__close {
  margin-bottom: 21px;
  margin-right: 20px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 5px;
}

.menu-button__close:hover .menu-button__burger, .menu-button__close:focus .menu-button__burger {
  fill: #d41443;
  stroke: #d41443;
}

.menu-button__burger {
  fill: #fff;
  stroke: #fff;
  transition: fill .25s cubic-bezier(.4, 0, .2, 1), stroke .25s cubic-bezier(.4, 0, .2, 1);
}

.menu-button__burger:hover, .menu-button__burger:focus {
  fill: #d41443;
  stroke: #d41443;
}

.menu-button__buy {
  width: 209px;
  margin-bottom: 19px;
  margin-left: auto;
  margin-right: auto;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
}

@media screen and (min-width: 768px) {
  .menu-button__buy {
    max-width: 186px;
  }
}

@media screen and (min-width: 1200px) {
  .menu-button__buy {
    max-width: 160px;
  }
}

@media screen and (min-width: 768px) {
  .menu-button__buy--header {
    max-width: 160px;
  }
}

@media screen and (min-width: 1200px) {
  .menu-button__buy--header {
    max-width: 159px;
  }
}

.menu-button__buy--table {
  margin-bottom: 0;
}

@media screen and (max-width: 767.98px) {
  .menu-button__buy--table {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .nav {
    z-index: 3;
    margin-left: 128px;
    display: flex;
    position: relative;
  }
}

@media screen and (max-width: 1199.98px) {
  .nav__list {
    border-top: 1px solid #e18298;
    display: block;
  }

  .nav__list:last-child {
    border-bottom: 1px solid #e18298;
  }
}

@media screen and (min-width: 1200px) {
  .nav__list:not(:last-child) {
    margin-right: 35px;
  }
}

.nav__link {
  max-width: 480px;
  color: #fff;
  margin-left: auto;
  margin-right: auto;
  padding-top: 11px;
  padding-bottom: 11px;
  padding-left: 20px;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.29;
  text-decoration: none;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.nav__link:hover, .nav__link:focus, .nav__link--selection {
  color: #d41443;
}

@media screen and (min-width: 1200px) {
  .nav__link {
    padding-left: 0;
    font-size: 16px;
    line-height: 1.3;
  }
}

.hero {
  height: 439px;
  background-color: #ffb8ca;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .hero {
    height: 326px;
    overflow: visible;
  }
}

@media screen and (min-width: 768px) and (min-width: 1200px) {
  .hero {
    height: 573px;
  }
}

.hero__container {
  height: 100%;
  padding-top: 131px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .hero__container {
    padding-top: 22px;
  }
}

@media screen and (min-width: 1200px) {
  .hero__container {
    padding-top: 76px;
  }
}

.hero__circle {
  width: 346px;
  height: 344px;
  background-color: #ffa5ba;
  border-radius: 50%;
  position: absolute;
  top: 20px;
  left: 11.25%;
