.hero__image {
  position: absolute;
  top: 9px;
  left: 81px;
}

@media screen and (min-width: 768px) {
  .hero__image {
    top: 34px;
    left: 74px;
  }
}

@media screen and (min-width: 1200px) {
  .hero__image {
    top: 56px;
    left: 119px;
  }
}

.hero__space {
  z-index: 2;
  position: relative;
}

@media screen and (min-width: 768px) {
  .hero__space {
    margin-bottom: 61px;
  }
}

@media screen and (min-width: 1200px) {
  .hero__space {
    margin-bottom: 108px;
  }
