.hero__title {
  letter-spacing: .04em;
  color: #fff;
  text-transform: uppercase;
  margin-bottom: 20px;
  font-family: Titan One, cursive;
  font-size: 26px;
  font-weight: 400;
  line-height: 1.15;
  animation: hero-title 2s cubic-bezier(.5, 1, .89, 1) both;
