@media screen and (min-width: 768px) {
  .hero__title {
    font-size: 22px;
  }
}

@media screen and (min-width: 1200px) {
  .hero__title {
    margin-bottom: 24px;
    font-size: 38px;
  }
}

.hero__title--accent {
  color: #d41443;
  font-size: 48px;
  line-height: .97;
  animation: hero-title-accent 2s cubic-bezier(.5, 1, .89, 1) 2s both;
}

@media screen and (min-width: 768px) {
  .hero__title--accent {
    font-size: 34px;
    line-height: .91;
  }
}

@media screen and (min-width: 1200px) {
  .hero__title--accent {
    font-size: 58px;
  }
}

.hero__button {
  cursor: pointer;
  background-color: #d41443;
  border: none;
  border-radius: 20px;
  padding: 9px 21px;
  font-size: 8px;
  line-height: 1.25;
  transition: filter .25s cubic-bezier(.4, 0, .2, 1);
