.hero__text-button {
  width: 18px;
  height: 18px;
  cursor: pointer;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
